<template>
  <AppRow permission="USERS_ADMINISTRATION">
    <v-col cols="12">
      <v-text-field
        v-model="searchText"
        solo
        flat
        hide-details="auto"
        background-color="#464646"
        label="Поиск пользователей"
        class="rounded-lg"
      >
        <template v-slot:prepend-inner>
          <v-icon class="mr-1">
            mdi-magnify
          </v-icon>
        </template>
      </v-text-field>
    </v-col>

    <v-col
      cols="12"
      sm="6"
      md="3"
    >
      <v-select
        v-model="deposited"
        :items="selectDeposited"
        item-value="key"
        item-text="text"
        solo
        flat
        hide-details="auto"
        clearable
        background-color="#464646"
        label="Наличие депозитов"
        class="rounded-lg"
        @change="firstPage"
      />
    </v-col>

    <v-col
      cols="12"
      sm="6"
      md="3"
    >
      <v-select
        v-model="role"
        :items="roles"
        solo
        flat
        hide-details="auto"
        clearable
        background-color="#464646"
        label="Роль"
        class="rounded-lg"
        @change="firstPage"
      />
    </v-col>

    <v-col
      cols="12"
      sm="6"
      md="3"
    >
      <v-select
        v-model="banned"
        :items="selectBanned"
        item-value="key"
        item-text="text"
        solo
        flat
        hide-details="auto"
        clearable
        background-color="#464646"
        label="Забанен/Не забанен"
        class="rounded-lg"
        @change="firstPage"
      />
    </v-col>

    <v-col
      cols="12"
      sm="6"
      md="3"
    >
      <DatePicker
        label="Дата регистрации (выбрать промежуток)"
        @save="saveDates"
      />
    </v-col>

    <v-col>
      <v-card
        flat
        class="px-4 pb-2"
      >
        <v-card-title class="py-7">
          Пользователи: 0
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="[{ id: 12, username: 'Alex', balance: 100, role: 'USER', createdAt: 79362187454 }]"
          :page.sync="page"
          :items-per-page="itemsPerPage"
          disable-sort
          no-data-text="Пользователи отсутствуют"
          no-results-text="Ничего не найдено"
          hide-default-footer
        >
          <template v-slot:[`item.img`]="{ item }">
            <v-avatar
              :color="item.img ? '' : '#c4c4c4'"
              size="36"
            >
              <img
                v-if="item.img"
                :src="item.img"
              >
              <v-icon
                v-else
                size="22"
                style="height: inherit"
              >
                mdi-account
              </v-icon>
            </v-avatar>
          </template>

          <template v-slot:[`item.createdAt`]="{ item }">
            {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
          </template>

          <template v-slot:[`item.action`]="{ item }">
            <v-btn
              :to="{ name: 'users', params: { id: item.id }}"
              depressed
              class="text-none"
              large
              height="36"
            >
              Подробнее
            </v-btn>
          </template>
        </v-data-table>
        <v-divider />

        <div class="text-center pt-3">
          <v-pagination
            v-model="page"
            :total-visible="9"
            :length="Math.ceil(1 / itemsPerPage)"
          />

          <v-select
            v-model="itemsPerPage"
            :items="[5, 10, 15]"
            label="Пользователей на странице"
            class="pa-4 pt-5"
            @change="firstPage"
          />
        </div>
      </v-card>
    </v-col>
  </AppRow>
</template>

<script>
import AppRow from '@/components/AppRow.vue';
import DatePicker from '@/components/DatePicker.vue';

export default {
  components: {
    AppRow,
    DatePicker,
  },
  data() {
    return {
      searchText: '',
      searchTimeout: null,
      search: '',
      deposited: null,
      selectDeposited: [
        { text: 'Были депозиты', key: true },
        { text: 'Не было депозитов', key: false },
      ],
      role: '',
      roles: ['ROLE_UNDEFINED', 'USER', 'ADMINISTRATOR', 'SUPERUSER', 'PARTNER', 'BLOGGER', 'BANNED', 'PUBLIC'],
      banned: null,
      selectBanned: [
        { text: 'Забанен', key: true },
        { text: 'Не забанен', key: false },
      ],
      registeredFrom: '',
      registeredTo: '',
      headers: [
        { text: '', value: 'img' },
        { text: 'ID:', value: 'id', class: 'white--text text-subtitle-2' },
        { text: 'Имя:', value: 'username', class: 'white--text text-subtitle-2' },
        { text: 'Баланс:', value: 'balance', class: 'white--text text-subtitle-2' },
        { text: 'Роль:', value: 'role', class: 'white--text text-subtitle-2' },
        { text: 'Дата регистрации:', value: 'createdAt', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
      page: 1,
      itemsPerPage: 5,
    };
  },
  watch: {
    searchText(value) {
      clearTimeout(this.searchTimeout);
      this.searchTimeout = setTimeout(() => {
        this.firstPage();
        this.search = value;
      }, 500);
    },
  },
  methods: {
    firstPage() {
      this.page = 1;
    },
    saveDates(dates) {
      [this.registeredFrom, this.registeredTo] = dates;
    },
  },
};
</script>
